import { Storage, StorageGetConfig, StorageGetOutput } from "@aws-amplify/storage";
import { useStore } from ".";

export function getLocalized<T extends Record<string, any> & {
    download?: boolean;
}>(name: string, extension: string, config?: StorageGetConfig<T>): StorageGetOutput<T> {
    const store = useStore();

    return Storage.get(`${name}_${store.state.locale}.${extension}`, config);
}
